html * {
  font-size: 16px;

  box-sizing: border-box !important;
}

.App { text-align: center;
  color: white;
  background-color: #eff6f6;

  min-width: 100vw;
  max-width: 100vw;
  min-height: 100vh;
  max-height: 100vh;
  margin: auto;

  display: flex;
  flex-direction: column;
  align-items: center;

  overflow: hidden;
}

.App-header {
  background-color: #005e61;

  min-width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-body {
  color: black;
  background-color: white;

  max-width: 70%;
  padding: 10px;

  flex-grow: 1;
}

.App-footer {
  background-color: #002f30;

  min-width: 100%;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


.App-footer > *{
  color: #367e7e;
  padding: 10px 0;
}

.Social-icon {
  height: 26px;
  margin-left: 1em;
  margin-right: 1em;
}

.Sitemap {
  color: #367e7e;
  border-left: 1px solid #367e7e;
  border-right: 1px solid #367e7e;
  padding-left: 5em;
  padding-right: 5em;
}

.Sitemap:first-of-type {
  border-left: 2px solid #367e7e;
}
.Sitemap:last-of-type {
  border-right: 2px solid #367e7e;
}

.Sitemap:hover {
  color: white;
}

@media only screen and (max-width: 768px){
  html * {
    font-size: 12px;
  }

  .App > *, .App-header > * {
    max-width: 100%;
    min-height: 5em;
    padding: 0px 1em;
  }

  .App-footer > * {
    max-width: 100%;
    border: none;
    padding: 1em;
  }

  .Sitemap, .Sitemap:first-of-type, .Sitemap:last-of-type {
    border: none;
    display: block;
  }
}

a {
  color: unset;
  text-decoration: none;
}